import { Component, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { Store } from '@ngrx/store';
import { AppState } from 'src/app/store/app.reducer';
import Swal from 'sweetalert2';
import { SearchPagination } from 'src/app/commons/interfaces/search.pagination';
import { FormControl, FormGroup } from '@angular/forms';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { SolicitudModel } from 'src/app/commons/models/aerc-models/solicitud-model';
import { EstadoSolicitudModel } from 'src/app/commons/models/aerc-models/estado-solicitud-model';
import { SolicitudService } from 'src/app/commons/services/aerc-services/solicitud.service';
import { listarSolicitudes, unsetSolicitud } from 'src/app/store/actions/aerc-actions/solicitud.actions';
import { getEstadoSolicitud } from 'src/app/store/actions/aerc-actions/estado-solicitud.actions';
import { TipoSolicitudModel } from 'src/app/commons/models/aerc-models/tipo-solicitud-model';
import { setVistaSolicitud } from '../../../store/actions/aerc-actions/solicitud.actions';
import { Router } from '@angular/router';
import { UtilAlert } from '../../../commons/util/util.alert';
import { MatDialog } from '@angular/material/dialog';
import { ComentariosDialogComponent } from 'src/app/commons/components/comentarios-dialog/comentarios-dialog.component';
import { PeriodoAcademicoService } from '../../../commons/services/aerc-services/periodo-academico.service';
import { PeriodoAcademicoModel } from '../../../commons/models/aerc-models/periodo-academico-model';
import { unsetEstadoSolicitud } from '../../../store/actions/aerc-actions/estado-solicitud.actions';
import { AprobadorAercService } from '../../../commons/services/aerc-services/aprobador-aerc.service';
import { AprobadorAercModel } from 'src/app/commons/models/aerc-models/aprobador-aerc.model';

@Component({
  selector: 'app-index-solicitudes',
  templateUrl: './index-solicitudes.component.html',
  styles: [
  ]
})
export class IndexSolicitudesComponent implements OnInit {

  public _subscriptionSolicitudState: Subscription;
  public _subscriptionEstadoSolicitudState: Subscription;
  public listadoSolicitudes: SolicitudModel[] = [];
  public aprobador: AprobadorAercModel[] = [];

  //*
  public isDoae: boolean = false;
  public isVra: boolean = false;
  public isEscuela: boolean = false;
  //*

  //*
  public formTopBar: FormGroup;
  public searchPagination: SearchPagination<string> = null;
  public pageSize: number = 10;
  public buscador = "";
  public page: number = 1;
  public totalElements = 0;
  public tipo: number = 0;
  public estado: number = 0;
  public periodo: number = 0;
  public listaEstadoSolicitud: Array<EstadoSolicitudModel> = [];
  public listaTipoSolicitud: Array<TipoSolicitudModel> = [];
  public listaPeriodoAcademico: Array<PeriodoAcademicoModel> = [];
  //*

  private timer = null;


  public loading = false;
  @BlockUI('windowSolicitudes') blockUI: NgBlockUI;


  constructor(
    private store: Store<AppState>,
    private solicitudService: SolicitudService,
    private router: Router,
    private alert: UtilAlert,
    private dialog: MatDialog,
    private periodoAcademicoService: PeriodoAcademicoService,
    private aprobadorService: AprobadorAercService

  ) { }

  //* INICIACION DE LA VENTANA
  //* ----------------------------------------------------------------------------------

  ngOnInit(): void {
    this.crearTopBar();
    this.listarSolicitudes();
    this.listarEstadoSolicitudes();
    this.listarTipoSolicitud();
    this.listarPeriodoAcademico();
    this._solicitudState();
    this._estadoSolicitudState();
    this.findAprobador();
  }

  ngOnDestroy(): void {
    this._subscriptionSolicitudState?.unsubscribe();
    this._subscriptionEstadoSolicitudState?.unsubscribe();
    unsetSolicitud();
    unsetEstadoSolicitud();
  }

  crearTopBar() {
    this.formTopBar = new FormGroup({
      estado: new FormControl(""),
      tipo: new FormControl(""),
      periodo: new FormControl(""),
      registros: new FormControl("")
    });

    this.formTopBar.get("estado").setValue(this.estado.toString());
    this.formTopBar.get("tipo").setValue(this.tipo.toString());
    this.formTopBar.get("periodo").setValue(this.periodo.toString());
    this.formTopBar.get("registros").setValue(this.pageSize.toString());
  }

  //* STATE REDUX
  //* ----------------------------------------------------------------------------------

  private _solicitudState() {
    this._subscriptionSolicitudState = this.store.select("solicitud").subscribe((state) => {
      this.loading = state.loading;

      if (state.searchPagination != null) {
        this.searchPagination = state.searchPagination;
        this.page = state.searchPagination.page;
        this.pageSize = state.searchPagination.records;
        this.buscador = state.searchPagination.seek;
        this.estado = state.estadoSolicitud;
        this.periodo = state.periodo;
        this.tipo = state.tipoSolicitud;
        if (this.formTopBar.get("tipo").value != this.tipo.toString()) {
          this.formTopBar.get("tipo").setValue(this.tipo);
          this.page = 1;
        }

        if (this.formTopBar.get("estado").value != this.estado.toString()) {
          this.formTopBar.get("estado").setValue(this.estado);
          this.page = 1;
        }

        if (this.formTopBar.get("registros").value != this.pageSize.toString()) {
          this.formTopBar.get("registros").setValue(this.pageSize);
        }

        if (this.formTopBar.get("periodo").value != this.periodo.toString()) {
          this.formTopBar.get("periodo").setValue(this.periodo);
        }
      }

      if (state.listadoSolicitudesPaginacion) {
        this.listadoSolicitudes = state.listadoSolicitudesPaginacion.content;
        this.totalElements = state.listadoSolicitudesPaginacion.totalElements;
      }

      if (this.blockUI.isActive != state.loading) {
        state.loading ? this.blockUI.start("Buscando solicitudes...") : this.blockUI.stop();
      }
    })
  }

  private _estadoSolicitudState() {
    this._subscriptionEstadoSolicitudState = this.store.select('estadoSolicitud').subscribe((state) => {
      if (state.estados) {
        this.listaEstadoSolicitud = state.estados;
      }
    })
  }

  //* METODOS PARA LISTAR Y CAMBIAR LAS SOLICITUDES
  //* ----------------------------------------------------------------------------------

  listarSolicitudes() {
    this.listadoSolicitudes = [];
    if (this.searchPagination == null) {
      this.searchPagination = {
        page: this.page,
        records: this.pageSize,
        seek: this.buscador,
      }
    } else {
      this.searchPagination = null
      this.listarSolicitudes();
    }
    this.store.dispatch(
      listarSolicitudes({ searchPagination: this.searchPagination, estadoSolicitud: this.estado, tipoSolicitud: this.tipo, periodo: this.periodo }))
  }

  listarEstadoSolicitudes() {
    this.store.dispatch(getEstadoSolicitud({ tipoSolicitud: 'ESTUDIANTE' }));
  }

  listarTipoSolicitud() {
    this.solicitudService.getAllTipoSolicitud().subscribe(result => {
      if (result != null) {
        this.listaTipoSolicitud = result;
      }
    })
  }

  listarPeriodoAcademico() {
    this.periodoAcademicoService.findAll().subscribe(result => {
      if (result != null) {
        this.listaPeriodoAcademico = result;
      }
    })
  }

  cambiarTipo(tipoServicio: number) {
    this.tipo = tipoServicio;
    this.listarSolicitudes();
  }

  cambiarEstado(estado: number) {
    this.estado = estado;
    this.listarSolicitudes();
  }

  cambiarPeriodo(periodo: number) {
    this.periodo = periodo;
    this.listarSolicitudes();
  }

  buscar(texto: string) {
    this.buscador = texto;
    if (this.searchPagination != null) {
      this.searchPagination = { ...this.searchPagination, seek: this.buscador }
    }
    /**
  .    * Se usa para evitar una petición cada vez que se presione una tecla y se haga solo cuando se termine de escribir.
     **/
    if (this.timer != null) clearTimeout(this.timer);
    this.timer = setTimeout(() => {
      this.listarSolicitudes();
      this.timer == null;
    }, 1000);
  }

  verSolicitud(solicitud: SolicitudModel) {
    console.log(solicitud)
    this.store.dispatch(setVistaSolicitud({ solicitud: solicitud }));
    this.router.navigate([`aerc/listado-solicitudes/ver-solicitud`]);
  }

  findAprobador() {
    this.aprobadorService.findAprobadorByAuth().subscribe(result => {
      if (result != null) {
        this.aprobador = result;
        this._findAprobador();
      }
    })
  }

  _findAprobador() {
    this.aprobador.forEach(a => {
      if (a.tipoAprobador.id == 6) {
        this.isDoae = true;
      } else if (a.tipoAprobador.id == 4) {
        this.isVra = true;
      } else {
        this.isEscuela = true;
      }
    })
  }

  aprobarSolicitud(solicitud: SolicitudModel) {
    Swal.fire({
      title: '¿Estás seguro?',
      text: "La solicitud sera aprobada.",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#02BC77',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirmar',
      cancelButtonText: 'Cancelar',
      reverseButtons: true
    }).then((result) => {
      this.blockUI.start("Aprobando solicitud...");
      if (result.isConfirmed) {
        this.solicitudService.aprobarSolicitud(solicitud).subscribe(result => {
          if (result != null) {
            this.blockUI.stop();
            this.alert.successSwalHtml("Solicitud aprobada con éxito");
            this.listarSolicitudes();
          }
        }, (_error) => {
          this.blockUI.stop();
          this.alert.toastError(_error?.error?.message);
        })
      }
    })
  }

  rechazarSolicitud(solicitud: SolicitudModel) {
    Swal.fire({
      title: '¿Estás seguro?',
      text: "La solicitud sera rechazada.",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#02BC77',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirmar',
      cancelButtonText: 'Cancelar',
      reverseButtons: true
    }).then((result) => {
      this.blockUI.start("Rechazando solicitud...");
      if (result.isConfirmed) {
        this.solicitudService.rechazarSolicitud(solicitud).subscribe(result => {
          if (result != null) {
            this.blockUI.stop();
            this.alert.successSwalHtml("Solicitud rechazada con éxito");
            this.listarSolicitudes();
          }
        }, (_error) => {
          this.blockUI.stop();
          this.alert.toastError(_error?.error?.message);
        })
      }
      this.blockUI.stop();
    })
  }

  rechazarSolicitudAVra(solicitud: SolicitudModel) {
    Swal.fire({
      title: '¿Estás seguro?',
      text: "La solicitud sera rechazada y enviada a la Vicerrectoría Académica para su posterior revisión.",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#02BC77',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirmar',
      cancelButtonText: 'Cancelar',
      reverseButtons: true
    }).then((result) => {
      this.blockUI.start("Rechazando solicitud...");
      if (result.isConfirmed) {
        this.solicitudService.rechazarSolicitudSuspensionFueraReglamento(solicitud).subscribe(result => {
          if (result != null) {
            this.blockUI.stop();
            this.alert.successSwalHtml("Solicitud rechazada con éxito");
            this.listarSolicitudes();
          }
        }, (_error) => {
          this.blockUI.stop();
          console.log(_error);
          this.alert.toastErrorHttp(_error);
        })
      }
      this.blockUI.stop();
    })
  }

  openDialogComentarios(solicitud: SolicitudModel, isAprobar: boolean) {
    const dialogRef = this.dialog.open(ComentariosDialogComponent, {
      data: { alumno: solicitud.usuario.nombre, tipo: solicitud.tipoSolicitud.descripcion, id: solicitud.id }
    });

    dialogRef.afterClosed().subscribe(result => {
      let comentarios: string = result;
      let solicitudTemp = { ...solicitud };
      if (solicitudTemp.isVa) {
        solicitudTemp.comentarioVra = comentarios;
      } else {
        solicitudTemp.comentarioEscuela = comentarios;
      }
      isAprobar ? this.aprobarSolicitud(solicitudTemp) : this.rechazarSolicitud(solicitudTemp);
    });
  }

  openDialogComentariosSuspensionVra(solicitud: SolicitudModel) {
    const dialogRef = this.dialog.open(ComentariosDialogComponent, {
      data: { alumno: solicitud.usuario.nombre, tipo: solicitud.tipoSolicitud.descripcion, id: solicitud.id }
    });

    dialogRef.afterClosed().subscribe(result => {
      let comentarios: string = result;
      let solicitudTemp = { ...solicitud };
      solicitudTemp.comentarioEscuela = comentarios;

      this.rechazarSolicitudAVra(solicitudTemp);
    });
  }

  habilitarEdicionSolicitud(solicitud: SolicitudModel) {
    Swal.fire({
      title: '¿Estás seguro?',
      text: "Se habilitará la edición de la solicitud para el estudiante.",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#02BC77',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirmar',
      cancelButtonText: 'Cancelar',
      reverseButtons: true
    }).then((result) => {
      if (result.isConfirmed) {
        this.solicitudService.habilitarEdicion(solicitud).subscribe(result => {
          if (result != null) {
            this.alert.successSwalHtml("Solicitud habilitada para su edición");
            this.listarSolicitudes();
          }
        }, (_error) => {
          this.alert.toastError(_error?.error?.message);
        })
      }
      this.blockUI.stop();
    })
  }

}
