import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { AlumnoModel } from '../../models/aerc-models/alumno.model';
import { UsuarioModel } from '../../models/aerc-models/usuario-model';


@Injectable({
  providedIn: 'root'
})
export class UsuarioService {


  private urlBase = `${environment.backend.aerc}api/usuario`;
  constructor(
    private http: HttpClient,
  ) { }

  public getDatosAlumnoByRutPasaporte(idBanner: string): Observable<AlumnoModel> {
    return this.http.get<AlumnoModel>(`${environment.backend.aerc}api/usuario/banner/get-datos-alumno-and-programas/${idBanner}`);
  }

  public getProgramaActivo(rut: string, tipo: string): Observable<Array<string>> {
    return this.http.get<Array<string>>(`${this.urlBase}/v1/find/alumno/programa-activo/${rut}/${tipo}`);
  }

}
