import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Store } from '@ngrx/store';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { AppService } from 'src/app/app.service';
import { AuthenticationService } from 'src/app/commons/services/authentication.service';
import { AppState } from 'src/app/store/app.reducer';
import { UtilAlert } from 'src/app/commons/util/util.alert';
import { MatStepper } from '@angular/material/stepper';
import { Subscription } from 'rxjs';
import Swal from 'sweetalert2';
import { ToastrService } from 'ngx-toastr';
import { ThrowStmt } from '@angular/compiler';
import { AlumnoModel } from 'src/app/commons/models/aerc-models/alumno.model';
import { ProgramaAlumno } from 'src/app/commons/interfaces/aerc-interfaces/programa-alumno.interface';
import { SubTask } from 'src/app/commons/interfaces/aerc-interfaces/subtask.interface';
import { Task } from 'src/app/commons/interfaces/aerc-interfaces/task.interface';
import { SolicitudModel } from 'src/app/commons/models/aerc-models/solicitud-model';
import { Router } from '@angular/router';
import { PreguntaRespuestaModel } from 'src/app/commons/models/aerc-models/pregunta-respuesta.model';
import { DocumentosModel } from 'src/app/commons/models/aerc-models/documentos-model';
import { UsuarioService } from 'src/app/commons/services/aerc-services/usuario.service';
import { SolicitudDetalleRespuestas } from 'src/app/commons/interfaces/aerc-interfaces/solicitud-detalle.interface';
import { unsetSolicitud } from 'src/app/store/actions/aerc-actions/solicitud.actions';
import { listarPreguntaSolicitud, unsetFormulario } from 'src/app/store/actions/aerc-actions/formulario.actions';
import { getEncuesta } from '../../../../store/actions/aerc-actions/formulario.actions';
import { getSolicitudesByTipo } from '../../../../store/actions/aerc-actions/solicitud.actions';
import { SolicitudDetalleService } from 'src/app/commons/services/aerc-services/solicitud-detalle.service';

@Component({
  selector: 'app-index-solicitud-detalle',
  templateUrl: './index-solicitud-detalle.component.html',
  styles: [
  ]
})
export class IndexSolicitudDetalleComponent implements OnInit {

  public preguntaRespuestasSolicitud: PreguntaRespuestaModel[];
  public preguntaRespuestasEncuesta: PreguntaRespuestaModel[];

  public alumnoBasico: AlumnoModel;
  public programasPosibles: ProgramaAlumno[] = [];
  public programaSeleccionado: ProgramaAlumno = null;

  public _subscriptionSolicitudState: Subscription;
  public _subscriptionFormularioState: Subscription;

  public loading = false;

  public solicitud: SolicitudModel = null;

  @BlockUI('stepper') blockUI: NgBlockUI;

  // FORMULARIO
  public formDatosAlumno: FormGroup;
  public taskEncuesta: Task[] = [];
  public taskSolicitud: Task[] = [];
  public solicitudDocumentacion: Array<DocumentosModel> = [];
  public solicitudAutocompletada = false;


  constructor(
    private appService: AppService,
    private usuarioService: UsuarioService,
    private store: Store<AppState>,
    private alert: UtilAlert,
    public router: Router,
    public auth: AuthenticationService,
    private detalleService: SolicitudDetalleService,
    private toastr: ToastrService,

  ) {
    this.appService.pageTitle = 'Revisión Solicitud';
  }

  //* INICIACION DE LA VENTANA
  //* ----------------------------------------------------------------------------------


  ngOnInit(): void {
    this.crearFormulario();
    this._solicitudState();

    this._formularioState();
    if (this.solicitud != null) {
      this.blockUI.start("Cargando información de la solicitud...");
      this.store.dispatch(listarPreguntaSolicitud({ tipoSolicitud: this.solicitud.tipoSolicitud.id, idSolicitud: this.solicitud.id }));
      this.store.dispatch(getEncuesta({ idSolicitud: this.solicitud.id }));
      this._cargarAlumno();
    }
  }

  ngOnDestroy(): void {
    this._subscriptionSolicitudState?.unsubscribe();
    this._subscriptionFormularioState?.unsubscribe();
    this.store.dispatch(unsetSolicitud());
    this.store.dispatch(unsetFormulario());
  }

  //* STATES DEL REDUX
  //* ----------------------------------------------------------------------------------

  private _solicitudState() {
    this._subscriptionSolicitudState = this.store.select("solicitud").subscribe((state) => {

      this.loading = state.loading;

      if (state.solicitud != null) {
        this.solicitud = state.solicitud;
      }

      if (this.solicitud == null) {
        this.router.navigate([`home`]);
      }
    })
  }


  private _cargarAlumno() {
    this.usuarioService.getDatosAlumnoByRutPasaporte(this.solicitud.usuario.username).subscribe(result => {
      this.alumnoBasico = result;
      this.programasPosibles = result.programas;
      this._cargarProgramaAlumno();
    })
  }

  private _cargarProgramaAlumno() {
    if (this.programaSeleccionado == null && this.solicitud != null) {
      this.programaSeleccionado = this.programasPosibles.filter(p => p.programaCod === this.solicitud.programaCodigo)[0];
      this.initFormDatosAlumno(this.alumnoBasico, this.programaSeleccionado);
      if (this.blockUI.isActive) {
        this.blockUI.stop();
      }
    } else {
      this.toastr.error("Ha ocurrido un error al momento de cargar la solicitud.");
      this.blockUI.stop();
    }
  }

  private _formularioState() {
    this._subscriptionFormularioState = this.store.select("formulario").subscribe((state) => {
      if (this.preguntaRespuestasEncuesta == null && state.listadoEncuesta != null && state.listadoEncuesta.length > 0) {
        this.preguntaRespuestasEncuesta = state.listadoEncuesta;
        this.crearFormularioTask(this.preguntaRespuestasEncuesta, 'ENCUESTA');
        // this._cargarSolicitud();
      }

      if (this.preguntaRespuestasSolicitud == null && state.listadoSolicitud != null && state.listadoSolicitud.length > 0) {
        this.preguntaRespuestasSolicitud = state.listadoSolicitud;
        this.crearFormularioTask(this.preguntaRespuestasSolicitud, 'SOLICITUD');
        // this._cargarSolicitud();
      }


    })
  }

  //* INICIACION DEL FORMULARIO ANTECEDENTES DEL ALUMNO
  //* ----------------------------------------------------------------------------------

  crearFormulario() {
    this.formDatosAlumno = new FormGroup({
      rutAlumno: new FormControl({ value: '', disabled: true }, [Validators.required]),
      nombre: new FormControl({ value: '', disabled: true }, [Validators.required]),
      carrera: new FormControl({ value: '', disabled: true }, [Validators.required]),
      escuela: new FormControl({ value: '', disabled: true }, [Validators.required]),
      correo: new FormControl({ value: '', disabled: true }, [Validators.required]),
      periodo: new FormControl({ value: '', disabled: true }, [Validators.required])
    });
  }

  private initFormDatosAlumno(alumno: AlumnoModel, programa: ProgramaAlumno): void {
    this.formDatosAlumno.get('rutAlumno').setValue(alumno.idBanner);
    this.formDatosAlumno.get('nombre').setValue(alumno.nombreCompleto);
    this.formDatosAlumno.get('carrera').setValue(programa.programaDesc);
    this.formDatosAlumno.get('escuela').setValue(programa.escuelaFacultadDesc);
    this.formDatosAlumno.get('correo').setValue(alumno.correoEdu);
    this.formDatosAlumno.get('periodo').setValue(programa.periodoAdmision);
  }


  //* FUNCION QUE INICIA EL PROCESO DE CREACIÓN DE SOLICITUD
  //* ----------------------------------------------------------------------------------

  crearFormularioTask(preguntaRespuestas: PreguntaRespuestaModel[], tipoTask: 'ENCUESTA' | 'SOLICITUD'): void {
    preguntaRespuestas.forEach(pregunta => {
      let respuestaTask: SubTask[] = [];

      pregunta.respuestas.forEach(respuesta => {
        let respuestaTemp: SubTask = {
          id: respuesta.idRespuesta,
          name: respuesta.descripcion,
          completed: false,
          color: 'primary',
          isText: respuesta.contieneTexto ? true : false,
          valueText: ''
        }
        respuestaTask.push(respuestaTemp);
      });

      let preguntaTemp: Task = {
        id: pregunta.idPregunta,
        number: preguntaRespuestas.indexOf(pregunta) + 1,
        name: pregunta.descripcion,
        completed: false,
        subtasks: respuestaTask
      }
      if (tipoTask == 'ENCUESTA') {
        this.taskEncuesta.push(preguntaTemp);
      }
      if (tipoTask == 'SOLICITUD') {
        this.taskSolicitud.push(preguntaTemp);
      }
    })
  }

  private _cargarSolicitud() {
    this.detalleService.getRespuestasBySolicitud(this.solicitud).subscribe(result => {
      this.autocompletarSolicitud(result);
      this.autocompletarDocumentacion(result);
    })
  }

  public volver(): void {
    this.router.navigate([`aerc/listado-solicitudes`]);
  }

  //* FUNCION QUE TRANSFORMA DE FILE A BASE64
  //* ----------------------------------------------------------------------------------

  private Base64toFile(base64: string, nombre: string, tipo: string) {
    let arr = base64.split(','),
      bstr = atob(arr[1]),
      n = bstr.length,
      u8arr = new Uint8Array(n);
    while (n--) {
      u8arr[n] = bstr.charCodeAt(n);
    }
    return new File([u8arr], nombre, { type: tipo });
  }

  downloadFile(data: File) {
    const blob = new Blob([data], { type: data.type });
    console.log(blob);
    const url = window.URL.createObjectURL(blob);
    window.open(url);
  }

  //* METODO MARA AUTOCOMPLETAR SOLICITUD SI SE ESTA EDITANDO
  //* ----------------------------------------------------------------------------------

  private autocompletarSolicitud(solicitudDetalle: SolicitudDetalleRespuestas): void {
    this.solicitudAutocompletada = true;
    solicitudDetalle.solicitudDetalle.forEach(a => {
      let subTaskSolicitud: SubTask = this.taskSolicitud.filter(t => t.id == a.pregunta.id)[0]?.subtasks.filter(st => st.id == a.respuesta.id)[0];
      let subTaskEncuesta: SubTask = this.taskEncuesta.filter(t => t.id == a.pregunta.id)[0]?.subtasks.filter(st => st.id == a.respuesta.id)[0];
      if (subTaskSolicitud != undefined) {
        subTaskSolicitud.completed = true;
        if (a.otro != null && subTaskSolicitud.isText) {
          subTaskSolicitud.valueText = a.otro;
        }
      }
      else if (subTaskEncuesta != undefined) {
        subTaskEncuesta.completed = true;
        if (a.otro != null && subTaskEncuesta.isText) {
          subTaskEncuesta.valueText = a.otro;
        }
      }
      subTaskSolicitud = null;
      subTaskEncuesta = null;
    })
  }

  private autocompletarDocumentacion(soliicitudDetalle: SolicitudDetalleRespuestas): void {
    soliicitudDetalle.solicitudDocumentacion.forEach(documento => {
      let doc = { ...documento };
      doc.file = this.Base64toFile(doc.base64, doc.nombre, doc.tipo);
      this.solicitudDocumentacion.push(doc);
    })
  }

}
