import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { IndexSolicitudesComponent } from './index-solicitudes.component';
import { IndexSolicitudDetalleComponent } from './index-solicitud-detalle/index-solicitud-detalle.component';

const routes: Routes = [
  { path: '', component: IndexSolicitudesComponent },
  { path: 'ver-solicitud', component: IndexSolicitudDetalleComponent }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class IndexSolicitudesRoutingModule { }
